<script setup>
  import { useMeiliSearchIndex } from '@/api/meilisearch/useMeiliSearchIndex.js'
  import { reactive, ref, watch } from 'vue'
  import { useStorage } from '@vueuse/core'
  import { strToInt } from '@/helpers/utils.js'
  import PaginationWidget from '@/components/PaginationWidget.vue'
  import SearchForm from './SearchForm.vue'
  import Table from './Table.vue'
  import columnsDef from './columnsDef.js'
  import { updateListUrl } from '@/helpers/meilisearch'

  const result = ref(0)
  const invoiceIndex = useMeiliSearchIndex('invoices')
  const loading = reactive({
    loading: false,
  })

  const defaultParams = {
    page: 0,
    items_per_page: 20,
    sort: 'date',
    sortDirection: 'desc',
    search: '',
    filters: {},
    columns: columnsDef.map((col) => col.name),
  }
  const paramsRef = useStorage(
    'invoiceList:params',
    defaultParams,
    localStorage,
    {
      mergeDefaults: true,
    }
  )
  // update defaults with current url params
  const urlParams = Object.fromEntries(
    new URLSearchParams(window.location.search)
  )
  // On assure que les valeurs sont des entiers
  if ('items_per_page' in urlParams) {
    urlParams.items_per_page = strToInt(urlParams.items_per_page)
  }
  if ('page' in urlParams) {
    urlParams.page = strToInt(urlParams.page)
  }
  // On assure que sortDirection est une valeur valide
  if ('sortDirection' in urlParams) {
    urlParams.sortDirection =
      urlParams.sortDirection === 'desc' ? 'desc' : 'asc'
  }
  paramsRef.value = { ...paramsRef.value, ...urlParams }
  const params = reactive(paramsRef.value)

  const handleSearch = async () => {
    loading.loading = true
    console.log('handleSearch', params)
    updateListUrl(params)

    result.value = await invoiceIndex.search(params.search, {
      limit: strToInt(params.items_per_page),
      offset: strToInt(params.page) * strToInt(params.items_per_page),
      sort: [`${params.sort}:${params.sortDirection}`],
      facets: ['paid_status', 'business_type.label'],

      // filter: "status = 'valid'",
    })
    loading.loading = false
  }

  const handleSort = async (sortColumn, sortDirection) => {
    params.sort = sortColumn
    params.sortDirection = sortDirection
  }

  watch(
    () => params.search,
    async (newSearch, oldSearch) => {
      if (newSearch.length <= 3 && newSearch.length >= oldSearch.length) {
        return
      }
      if (params.page !== 0) {
        params.page = 0
        return
      }

      handleSearch()
    }
  )

  watch(
    () => [
      params.sort,
      params.sortDirection,
      params.page,
      params.items_per_page,
      params.columns,
    ],
    async (newValues, oldValues) => {
      // Si on change le nombre d'éléments par page,
      // on passe à la page 0
      if (newValues[3] != oldValues[3] && params.page > 0) {
        params.page = 0
        return
      }
      handleSearch()
    }
  )
  await handleSearch()
</script>
<template>
  <SearchForm
    :params="params"
    :invoices="result.hits"
    :facets="result.facetDistribution"
    v-model:search="params.search"
    @search="handleSearch"
    @sort="handleSort"
  />
  <PaginationWidget
    :items="result.hits"
    :numItems="result.estimatedTotalHits"
    v-model:itemsPerPage="params.items_per_page"
    v-model:page="params.page"
    v-model:columns="params.columns"
    :columns-def="columnsDef"
  />
  <div v-if="loading.loading">Chargement des données ...</div>
  <div v-else class="table_container">
    <Table :invoices="result.hits" :params="params" @sort="handleSort" />
  </div>
</template>
