<script setup>
  import { computed } from 'vue'
  import { integerToCurrency } from '@/helpers/utils'
  import Icon from '@/components/Icon.vue'
  import columnsDef from './columnsDef.js'

  const props = defineProps({
    invoices: { type: Array },
    params: { type: Object },
  })
  const emits = defineEmits(['sort'])

  const displayedColumns = computed(() => {
    return columnsDef.filter((column) => {
      return props.params.columns.includes(column.name)
    })
  })
  const totals = computed(() => {
    console.log('computing totals')
    const result = {}
    if (props.params.columns.includes('ht')) {
      result.ht = props.invoices.reduce((acc, invoice) => +acc + invoice.ht, 0)
    }
    if (props.params.columns.includes('tva')) {
      result.tva = props.invoices.reduce(
        (acc, invoice) => +acc + invoice.tva,
        0
      )
    }
    if (props.params.columns.includes('ttc')) {
      result.ttc = props.invoices.reduce(
        (acc, invoice) => +acc + invoice.ttc,
        0
      )
    }
    return result
  })
  const runSort = (col) => {
    const columnDef = columnsDef[col]
    const sortColumn = columnDef.sort
    if (!sortColumn) {
      console.error('No sort defined for column', col)
      return
    }
    const current = props.params.sort
    let sortDirection = 'asc'
    if (current == sortColumn) {
      if (props.params.sortDirection == 'asc') {
        sortDirection = 'desc'
      } else {
        sortDirection = 'asc'
      }
    }
    emits('sort', sortColumn, sortDirection)
  }
  const computeSortCss = (columnDef) => {
    let result = 'icon'
    const sortColumn = columnDef.sort
    const current = props.params.sort
    if (current == sortColumn) {
      result += ' current '
      if (props.params.sortDirection == 'asc') {
        result += 'asc'
      } else {
        result += 'desc'
      }
    }
    return result
  }
  const getSortIcon = (columnDef) => {
    const sortColumn = columnDef.sort
    const current = props.params.sort
    if (current == sortColumn) {
      if (props.params.sortDirection == 'asc') {
        return 'sort-asc'
      } else {
        return 'sort-desc'
      }
    } else {
      return 'sort-arrow'
    }
  }

  const totalColspan = computed(() => {
    let val = 0
    displayedColumns.value.forEach((columnDef, index) => {
      if (['ht', 'tva', 'ttc'].includes(columnDef.name)) {
        if (val > 0) {
          val = Math.min(index, val)
        } else {
          val = index
        }
      }
    })
    return val
  })
</script>
<template>
  <table>
    <thead>
      <th scope="col" v-for="col in displayedColumns" :key="col.name">
        <a
          :class="computeSortCss(col)"
          v-if="col.sort"
          @click="runSort(col.name)"
        >
          <Icon :name="getSortIcon(col)" />
          {{ col.title }}
        </a>
        <template v-else>
          {{ col.title }}
        </template>
      </th>
    </thead>
    <tr class="row_recap">
      <th scope="row" :colspan="totalColspan" class="col_text">Total</th>
      <td v-if="params.columns.includes('ht')" class="col_number">
        {{ integerToCurrency(totals.ht) }}
      </td>
      <td v-if="params.columns.includes('tva')" class="col_number">
        {{ integerToCurrency(totals.tva) }}
      </td>
      <td v-if="params.columns.includes('ttc')" class="col_number">
        {{ integerToCurrency(totals.ttc) }}
      </td>
    </tr>
    <tbody>
      <tr v-for="invoice in invoices" :key="invoice.id">
        <template v-for="col in displayedColumns">
          <component
            :is="col.cellComponent"
            v-bind="col.componentOptions"
            :task="invoice"
          ></component>
        </template>
      </tr>
    </tbody>
  </table>
</template>
