<script setup>
  import { useVModels } from '@vueuse/core'
  import { ref, computed } from 'vue'
  import Icon from './Icon.vue'
  import Button from './Button.vue'
  import ModalLayout from '@/layouts/ModalLayout.vue'

  const props = defineProps({
    items: { type: Array, required: true },
    numItems: { type: [Number, String], required: true },
    itemsPerPage: { type: [Number, String], required: true },
    page: { type: [Number, String], required: true },
    columnsDef: { type: Object, required: false },
    columns: { type: Array, required: false },
  })

  const emits = defineEmits([])
  const { itemsPerPage, page, columns } = useVModels(props, emits)

  const itemsPerPageOptions = [
    { value: 10, label: '10 par page' },
    { value: 25, label: '25 par page' },
    { value: 50, label: '50 par page' },
    { value: 100, label: '100 par page' },
    { value: 200, label: '200 par page' },
    { value: 100000, label: 'Tous' },
  ]

  const currentPage = computed(() => page.value + 1)
  const numPages = computed(() =>
    Math.ceil(props.numItems / itemsPerPage.value)
  )

  const allPages = computed(() => {
    const result = []
    const start = Math.max(1, currentPage.value - 2)
    const end = Math.min(numPages.value, currentPage.value + 2)
    for (let i = start; i < currentPage.value; i++) {
      result.push(i)
    }
    result.push(currentPage.value)
    for (let i = currentPage.value + 1; i <= end; i++) {
      result.push(i)
    }
    return result
  })

  const showColumnDropdown = ref(false)
  const toggleDropdown = () => {
    showColumnDropdown.value = !showColumnDropdown.value
  }
</script>
<template>
  <div class="pager display_selector">
    <div>
      <nav aria-label="Pagination">
        <ul>
          <li v-if="currentPage > 1">
            <a
              class="btn"
              data-action="show_page"
              :data-page="currentPage - 1"
              href="javascript:void(0);"
              :title="`Voir la page précédente (${currentPage - 1})`"
              aria-label="Voir la page précédente"
              @click="page = currentPage - 2"
            >
              <Icon name="chevron-left" />
            </a>
          </li>
          <li v-if="currentPage > 3"><span class="spacer">…</span></li>
          <template v-for="numPage in allPages" :key="numPage">
            <li>
              <span
                v-if="numPage == currentPage"
                class="current"
                :title="`Page en cours : page ${numPage}`"
                :aria-label="`Page en cours : page ${numPage}`"
              >
                {{ numPage }}
              </span>
              <a
                v-else
                class="btn"
                data-action="show_page"
                :data-page="numPage"
                :title="`Aller à la page ${numPage}`"
                :aria-label="`Aller à la page ${numPage}`"
                @click="page = numPage - 1"
                href="javascript:void(0);"
              >
                {{ numPage }}
              </a>
            </li>
          </template>
          <li v-if="numPages - currentPage > 2">
            <span class="spacer">…</span>
          </li>
          <li v-if="currentPage < numPages">
            <a
              class="btn"
              data-action="show_page"
              :data-page="currentPage + 1"
              href="javascript:void(0);"
              :title="`Voir la page suivante (${currentPage + 1})`"
              aria-label="Voir la page suivante"
              @click="page = currentPage"
            >
              <Icon name="chevron-right" />
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <form>
      <div>
        <label for="items_per_page_top" class="screen-reader-text">
          Éléments affichés
        </label>
        <select id="items_per_page_top" v-model="itemsPerPage">
          <option
            :value="item.value"
            :key="item.value"
            v-for="item in itemsPerPageOptions"
          >
            {{ item.label }}
          </option>
        </select>
        <Button
          icon="file-list"
          @click="toggleDropdown"
          label="Afficher la liste des colonnes"
          :show-label="false"
        />
        <ModalLayout v-if="showColumnDropdown" @close="toggleDropdown">
          <template v-slot:header>
            <h3>Configurer la liste des colonnes à afficher</h3>
          </template>

          <template v-slot:body>
            <div v-for="column in columnsDef" :key="column.name">
              <input type="checkbox" :value="column.name" v-model="columns" />
              <label :for="column.name">{{ column.title }}</label>
            </div>
          </template>
        </ModalLayout>
      </div>
    </form>
  </div>
</template>
